.modal {
  border-radius: 4px;
  background-color: white;
  padding: 24px;
  box-shadow: none;
  width: 85%;
  height: 85%;
}

.overlay {
  background-color: rgba(47, 56, 56, 0.5);
}

@media (max-width: 1280px) {
  .modal {
    min-width: 1040px;
    height: 740px;
  }
}

@media (min-width: 1281px) {
  .modal {
    max-width: 1660px;
  }
}
