.consent-section {
  width: 127%;
  background: #a6a9b524;
  margin: 20px 0 40px -90px;
  padding: 45px 91px 29px 91px;
}

.third-party-info {
  margin: 60px 0 60px 0;
  display: flex;
}

.third-party-info > div:first-of-type {
  display: flex;
}

.third-party-name-age > div:first-of-type {
  font-family: Lato;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  text-transform: uppercase;
  display: block;
  line-height: 1.17;
  -webkit-letter-spacing: 1.25px;
  -moz-letter-spacing: 1.25px;
  -ms-letter-spacing: 1.25px;
  letter-spacing: 1.25px;
  color: #848899;
  margin-bottom: 8px;
}

.third-party-address {
  padding-left: 105px;
}

.third-party-address > div:first-of-type {
  font-family: Lato;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  text-transform: uppercase;
  display: block;
  line-height: 1.17;
  -webkit-letter-spacing: 1.25px;
  -moz-letter-spacing: 1.25px;
  -ms-letter-spacing: 1.25px;
  letter-spacing: 1.25px;
  color: #848899;
  margin-bottom: 8px;
}

@media only screen and (max-width: 768px) {
  .third-party-address > div:first-of-type {
    font-size: 25px;
  }

  .third-party-name-age > div:first-of-type {
    font-size: 25px;
  }

  .third-party-info {
    font-size: 30px;
  }
}
